// 引入Vuex从'revuex'
import { createStore } from 'vuex';

// 使用createStore函数创建并导出一个新的 Vuex store 实例
export default createStore({
  // 定义此应用的全局状态
  state: {
    showSignUp: false, 
    showSwitch: false,
  },
  
  // 计算衍生出的状态
  // state的计算属性，用于获取state中的某个状态的值
  getters: {
    showSignUp: state => state.showSignUp
  },
  
  // mutations 是用于改变状态的方法
  // 你只能通过提交（commit）mutation来改变状态
  mutations: {
    // 这个 mutation 会将 showSignUp 的值在 true 和 false 之间切换
    toggleShowSignUp(state) {
        state.showSignUp = !state.showSignUp;
    },
    // 这个 mutation 会将 showSwitch 的值在 true 和 false 之间切换
    toggleShowSwitch(state) {
        state.showSwitch = !state.showSwitch;
    },
  },
  
  // Actions 是用于异步操作的函数
  // Actions 提交的是 mutations，而不是直接更改状态
  actions: {
    // 设定一个延时器，待 1100 毫秒后，提交（commit）一个叫做 toggleShowSwitch 的 mutation
    delayToggleShowSwitch({ commit }) {
        setTimeout(() => {
            commit('toggleShowSwitch');
        }, 1120);
    },
  },
  
  // 模块
  // Vuex 允许我们将 store 分割成模块（module）
  // 每个模块拥有自己的 state、mutation、action、getter
  // 甚至是嵌套子模块——就像是一个小型的 Vuex store
  modules: {
    // 你的模块
  },
});