<template>
	<!-- <router-view /> 是 Vue Router 提供的组件，它用于渲染跟当前路径匹配的组件。 -->
	<!-- 根据路由配置，当你的路径变化时，对应路径的组件将会被渲染在 <router-view /> 的位置上 -->
	<router-view />
</template>

<!-- 这是类型为 "module" 的 script 标签，用于写 Vue 组件的逻辑，所有的 Vue 组件逻辑，如 data、methods、computed 等等都将在此书写 -->
<script>
	// 导入
	import { ref, onMounted } from 'vue';

	// 用 provide 函数将 api 对象注入到应用程序中，这样其他组件就可以通过 inject 函数来获取 api 对象了
	import { provide } from 'vue';
	import api from '../public/api/api.js';

	// 使用
	export default {
		setup() {
			// provide 函数需要在 setup（） 方法中使用
			provide('api', api);
			// 使用 reactive API - ref
			const count = ref(0);

			// 生命周期钩子 - onMounted 在挂载（即插入 DOM）结束后被调用
			// 在 onMounted 中，我们一般进行一些依赖于 DOM 的逻辑，
			// 因为这个时候我们可以确认组件已经完全挂载到 DOM 上了
			onMounted(() => {
				console.log('The component is mounted.');
			});

			// setup 必须返回一个对象，这个对象将会被合并到组件实例上
			// 因此，我们在这里返回的变量和函数，将可以直接通过 this 调用
			return {
				count,
			};
		},
	};
</script>
<style lang="less"></style>
<!-- Vue 组件的生命周期主要包括以下几个阶段：
	1. 创建阶段
	2. 挂载阶段
	3. 更新阶段
	4. 销毁阶段

**创建 (Creation)**：
在创建阶段，组件正在被设置和配置。主要的生命周期钩子函数包括：
beforeCreate()：在实例初始化之后、数据观测和事件配置之前被调用。
created()：在实例创建完成后被立即调用，在这一步，实例已完成以下的配置：数据观测、属性计算、方法和事件回调。然而，挂载阶段还没开始，$el 属性目前不可见。

**挂载 (Mounting)**：
在挂载阶段，模板被编译并挂载到 DOM 上。主要的生命周期钩子函数包括：
beforeMount()：在挂载开始之前被调用，此时已完成模板编译。
mounted()：在挂载完成之后被调用，在这一步，我们可以确认组件已经完全挂载到 DOM 上了。

**更新 (Updating)**：
在更新阶段，响应系统检测到数据变化并更新 DOM。主要的生命周期钩子函数包括：
beforeUpdate()：在响应式数据更新 DOM 之前触发。
updated()：在数据更改引起视图更新完成后触发。

**销毁 (Destruction)**：
在销毁阶段，实例被销毁，内存被释放。主要的生命周期钩子函数包括：
beforeDestroy()：在实例销毁之前触发。在这一步，实例仍然完全可用。
destroyed()：在 Vue 实例销毁后调用。 -->

<!-- 
Vue 3 提供了更便利的生命周期函数:
onBeforeMount()
onMounted()
onBeforeUpdate()
onUpdated()
onBeforeUnmount()
onUnmounted() -->
