// 导入路由所需的标准库
import { createRouter, createWebHashHistory,createWebHistory,RouteRecordRaw } from 'vue-router';

// 路由配置，每一个元素对应一条路由规则
const routes: Array<RouteRecordRaw> = [
  { path: '/front', redirect: '/front/login' },
  { path: '/', redirect: '/front/login' },
  {
    // 路由路径
    path: '/front/login',
    name:'frontLogin',
    // 绑定到该路由的 Vue 组件，这里通过  异步  的方式导入
    component: () => import('@/views/front/login/login.vue'),
    // 嵌套路由，如果有需要可以加入，每一个children中的路由都会显示在对应父路由的 <router-view /> 中
    // children: [
    //   // 如果这个网址是 '/home/profile'，用户将会看到被渲染的 UserProfile 组件
    //   { path: 'profile', component: UserProfile },
    // ],
    // 路由别名，当用户访问 '/' 的时候将会和访问 '/front/login' 同样的效果
    // alias: '/',
  },
  // 重定向，当用户访问 '/login' 时将自动跳转到 '/'
  // { path: '/login', redirect: '/' },
  // // 带参数的路由，可以通过 $route.params 获取参数
  // { path: '/user/:userId', component: User },
  // {
  //   path: '/index',
  //   name: 'index',
  //   component: () => import('@/views/index/index.vue'),
  // }
  {
    path: '/front/index',
    name: 'frontIndex',
    component: () => import('@/views/front/index/index.vue'),
  },
  { path: '/admin', redirect: '/admin/login' },
  {
    path: '/admin/login',
    name: 'AdminLogin',
    component: () => import('@/views/admin/login/login.vue'),
  },  
  {
    path: '/admin/index',
    name: 'AdminIndex',
    component: () => import('@/views/admin/index/index.vue'),
  },
  {
    path: '/css',
    component: () => import('@/views/front/index/components/ICSS.vue'),
  }
];

// 创建 router 实例 createRouter 是 Vue Router 提供的创建路由的方法，需要传入一个配置对象。
const router = createRouter({
    // 使用 hash 模式
    // '#/' 形式的 URL
    // createWebHashHistory 创建一个路由模式为 hash 的历史记录模式对象。hash模式会在 URL 添加 `#/`。
    // history: createWebHashHistory(),

    // 使用HTML5 history模式
    history: createWebHistory(),
    routes,  // 注入路由配置
});

// 导出 router
export default router;

